<template>
  <div class="baikeItem">
    <div class="title">
      <h2 class="number">{{index|getNumber}}</h2>
      <div class="sub">
        <h3>{{itemData.name}}</h3>
        <!-- <p>有购房资质了吗？资金准备好了吗？开始寻找房源吧</p> -->
      </div>
    </div>
    <div class="warp_item">
      <div class="item" v-for="subItem,idx in list" v-if="idx < 2 && !isShowMreo" @click="gotoDetail(subItem)">
        <span>{{subItem.title}}</span>
        <!-- <p>目前，北京的二手商品住宅、二手住宅平房等已经限购，购房者需满足资格才能购买。</p> -->
      </div>
    </div>
    <div class="warp_item">
      <div class="item" v-for="subItem,idx in list" v-if="isShowMreo">
        <span>{{subItem.title}}</span>
        <!-- <p>目前，北京的二手商品住宅、二手住宅平房等已经限购，购房者需满足资格才能购买。</p> -->
      </div>
    </div>
    <div class="down" @click="setShowMreo()">
      <img src="../../../images/main/down_icon.png" v-if="!isShowMreo">
      <img src="../../../images/main/up_icon.png" v-if="isShowMreo">
    </div>
  </div>
</template>

<script>
import {getStore,imgBaseUrl} from '../../../components/common/mUtils'
export default {
  data(){
    return{
      imgBaseUrl,
      data:[],
      list:[],
      isShowMreo:false,
      selfIndex:0,
    }
  },
  props:['itemData','index'],
  filters:{
    parseFloat(a){
      return parseFloat(a)
    },
    getNumber(idx){
      ++idx
      if (idx < 10) {
        idx = '0'+idx
      }
      return idx;
    }
  },
  computed:{
    getUserAvatar:function(){
      if(this.itemData){
        if (this.itemData.avatarPath == '') {
          return "http://jimg.07358.com/Fnhc3IKMv02O5Udciy3gsPCf6C6y"
        }else {
          return imgBaseUrl + this.itemData.avatarPath
        }
      }else {
        return "http://jimg.07358.com/Fnhc3IKMv02O5Udciy3gsPCf6C6y"
      }
    },
    getImgs(){
      let imags = this.itemData.imgs.split(',')
      return imags[0];
    }
  },
  created(){
  },
  mounted(){
    this.getData()
  },
  components:{
  },
  methods: {
      setShowMreo(){
        this.isShowMreo = !this.isShowMreo
      },
      gotoDetail(subItem){
        let p = {path: '/baikeDetail_pc', query:{id: subItem.ask_baike_id}}
        this.$router.push(p)
      },
      getData(){
        // Indicator.open();
        let getUrl = this.CFWConfig.APIHost + '/v3/ask/baike?sid='+this.itemData.ask_typezi_id
        this.$http.get(getUrl).then(response => {
          let code = response.data.code
          if (code == 200) {
            this.list = response.data.result
          }
          // Indicator.close();
        }, response => {
          Toast('请求失败');
          console.log(response);
          // Indicator.close();
        });
    }
  },
  watch: {
    itemData(){
      this.getData()
    }
  }

}

</script>

<style scoped>

.baikeItem{
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  background-color: #f8f8f9;
}

.baikeItem .title{
  height: 60px;
  width: 840px;
  padding: 20px 0px 10px;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  background-color: #f8f8f9;
}

.baikeItem .title .number{
  font-size: 60px;
  color: #000;
  margin-left: 30px;
}

.baikeItem .title .sub{
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  text-align: left;
}

.baikeItem .title .sub h3{
  font-size: 18px;
  line-height: 24px;
  font-weight: bold;
}

.baikeItem .title .sub p{
  font-size: 13px;
  line-height: 26px;
}

.baikeItem .warp_item{
  width: 840px;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  background-color: #f8f8f9;
  flex-wrap: wrap;
}

.baikeItem .item{
  width: 330px;
  margin-left: 20px;
  padding: 25px 30px;
  border-bottom: 4px solid #ebecec;
  background-color: #fff;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  margin-bottom: 30px;
}

.baikeItem .item span{
  font-weight: bold;
  font-size: 16px;
  text-align: left;
  font-family: "Hiragino Sans GB", "Microsoft Yahei UI", "Microsoft Yahei", 微软雅黑, "Segoe UI", Tahoma, 宋体b8b体, SimSun, sans-serif;
  overflow:hidden;
  text-overflow:ellipsis;
  display:-webkit-box;
  -webkit-box-orient:vertical;
  -webkit-line-clamp:2;
}

.baikeItem .item p{
  font-size: 14px;
  text-align: left;
  margin-top: 15px;
  line-height: 20px;
  overflow:hidden;
  text-overflow:ellipsis;
  display:-webkit-box;
  -webkit-box-orient:vertical;
  -webkit-line-clamp:3;
}

.baikeItem .down{

}

.baikeItem .down img{
  width: 30px;
  height: 30px;
}

</style>



