<template>
  <div id="baike_pc">
    <PCHeader></PCHeader>

    <section class="nav">
      <PCCommonNav :tid="3"></PCCommonNav>
    </section>

    <section class="main_center">
      <div class="left">
        <mt-swipe :auto="4000" id="pc_baikeSwipe" v-if="swipeData.length">
          <mt-swipe-item v-for = "(item, index) in swipeData" class="swiperItem">
              <img @click = "gotoDetail(65)" src="../../images/baike/baike_ad_1.png">
          </mt-swipe-item>
          <i>广告</i>
        </mt-swipe>
        <div class="subAd">
          <div class="ad_item" @click="gotoDetail(80)"><img src="../../images/baike/baike_ad_2.png"></div>
          <div class="ad_item" @click="gotoDetail(84)"><img src="../../images/baike/baike_ad_3.png"></div>
          <div class="ad_item" @click="gotoDetail(9)"><img src="../../images/baike/baike_ad_4.png"></div>
        </div>
        <div class="ask_category">
          <div class="warp">
            <span v-for="item in ask_category" :class="item.ask_type_id == sid ? 'selectd':'' " @click="setType(item)">{{item.name}}</span></div>
        </div>

        <section class="baike_list" style="margin-top:0px;" v-for="item,index in typeList">
          <PCBaikeItem :itemData="item" :index="index"></PCBaikeItem>
        </section>

      </div>
      <div class="right">

        <section class="category">
          <h3><i>百科</i>分类</h3>
          <div class="item" v-for="item in ask_category">
              <PCBaikeType :itemData="item" @itemAction="baikeTypeAction"></PCBaikeType>
          </div>
        </section>

      </div>
    </section>

    <PCFooter></PCFooter>

  </div>
</template>

<script>
import { Toast,Indicator} from 'mint-ui'
import {getStore,imgBaseUrl} from '../../components/common/mUtils'
import PCHeader from '../../components/header/PCHeader'
import PCFooter from '../../components/footer/PCFooter'
import PCBaikeItem from '../../page/home/children/PCBaikeItem'
import PCBaikeType from '../../page/home/children/PCBaikeType'
import PCCommonNav from '../../components/common/PCCommonNav'
export default {
  data(){
    return{
      imgBaseUrl,
      userToken:'',
      userID:'',
      isLogin:false,
      swipeData:[
        {
          "img": "//p.07358.com/cfw/20170629/20170629160121_63143.jpeg",
          "guid": "adv0",
          "title": "郴房网",
          "href": "http://www.07358.com/zhuanti/2017/cfzb5/"
        },
        {
          "img": "//p.07358.com/cfw/20170722/20170722112037_87030.jpg",
          "guid": "adv0",
          "title": "郴房网",
          "href": "http://www.07358.com/zhuanti/2017/cfzb5/"
        }
      ],
      list:[],
      ask_category:[],
      sid:1,
      typeList:[]
    }
  },
  created(){
    this.userToken = getStore('userToken')
    this.userID = getStore("userID")
    if (this.userID) {
      this.isLogin = true
    }

    // 检测是不是移动端
    this.isMobile()
  },
  filters:{
    getNumber(idx){
      ++idx
      if (idx < 10) {
        idx = '0'+idx
      }
      return idx;
    }
  },
  mounted(){

    // 获取热导航
    this.getNav()

    // 获取最新问题
    this.getTypeList()

    // 获取滚动广告
    this.getSwipeData()

  },
  components:{
    PCHeader,
    PCFooter,
    PCBaikeItem,
    PCBaikeType,
    PCCommonNav
  },
  computed:{
    getUserAvatar:function(){
      if(this.userInfo){
        return imgBaseUrl + this.userInfo.avatarPath
      }else {
        return "http://qpic.07358.com/FiPMqTlrYzi_8naGgKIfj4h9wGLi"
      }
    }
  },
  methods: {
    gotoDetail(bid){
        let p = {path: '/baikeDetail_pc', query:{id: bid}}
        this.$router.push(p)
    },
    gotoAddress(path,login){
      if (login) {
        if (this.isLogin) {
          this.$router.push(path)
        }else {
          this.$router.push('/login_pc')
        }
      }else {
          this.$router.push(path)
      }
    },
    setType(item){
      this.sid = item.ask_type_id
      // this.searchKey.page = 1
      this.getTypeList()
    },
    // 获取子类列表
    getTypeList(){
      Indicator.open();
      let getUrl = this.CFWConfig.APIHost + '/v3/ask/zi?id='+this.sid
      this.$http.get(getUrl).then(response => {
        let code = response.data.code
        if (code == 200) {
          this.typeList = response.data.result
        }
        Indicator.close();
      }, response => {
        Toast('请求失败');
        console.log(response);
        Indicator.close();
      });
    },
    // 导航分
    getNav(){
      Indicator.open();
      let getUrl = this.CFWConfig.APIHost + '/v3/ask/fu'
      // console.log(getUrl)
      this.$http.get(getUrl).then(response => {
        let code = response.data.code
        if (code == 200) {
          // this.nav = response.data.result
          this.ask_category = response.data.result
        }
        Indicator.close();
      }, response => {
        Toast('请求失败');
        console.log(response);
        Indicator.close();
      });
    },
    // 获取滚动广告
    getSwipeData(){
      Indicator.open();
      let getUrl = this.CFWConfig.APIHost + '/v2/adv/news?navId=600'
      // console.log(getUrl)
      this.$http.get(getUrl).then(response => {
        let code = response.status
        if (code == 200) {
          this.swipeData = response.body
        }
        Indicator.close();
      }, response => {
        Toast('请求失败');
        console.log(response);
        Indicator.close();
      });
    },
    baikeTypeAction(subItem){
      this.$router.push({path: '/baikeList_pc', query:{sid:subItem.ask_typezi_id,typeTitle:subItem.titleName+'|'+subItem.name}})
    },
    isMobile(){
      var sUserAgent = navigator.userAgent;
    	var mobileAgents=['Android','iPhone','Symbian','WindowsPhone','iPod','BlackBerry','Windows CE'];
    	var goUrl = 0;
    	for( var i=0;i<mobileAgents.length;i++){
    			if(sUserAgent.indexOf(mobileAgents[i]) > -1){
    					goUrl = 1;
    					break;
    			}
    	}
      // console.log(goUrl)
    	if (goUrl != 1){
        console.log('pc')
    	}else {
    	  console.log('Mobile')
        this.$router.push('home')
    	}
    }
  },

}
</script>

<style scoped>
/*引入公共样式*/
/*@import '../../style/common/style.css';*/
#baike_pc{
  background: #eee;
}
#baike_pc .nav,.main_center{
  max-width: 1200px;
  margin: 15px auto 0px;
}
#baike_pc .header_search{
  max-width: 680px;
  margin: 0px auto;
}

.main_center{
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
}

.main_center .left{
  flex-grow: 1;
  margin-right: 20px;
  max-width: 880px;
  /*background-color: #fff;*/
}

.main_center .left  section{
  background-color: #fff;
  padding: 10px 20px;
}

.main_center .right  section{
  background-color: #fff;
  padding: 20px 20px;
}
#pc_baikeSwipe{
  height: 380px;
}
#pc_baikeSwipe img{
  width: 100%;
  height: 100%;
}

#pc_baikeSwipe i{
  position: absolute;
  bottom: 2px;
  right: 2px;
  font-size: 12px;
  color: #fff;
  font-style: normal;
  padding: 3px 5px;
  background-color: rgb(140, 140, 140);
}

.main_center .left .subAd{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 20px 10px 0px;
  background-color: #fff;
}

.main_center .left .subAd .ad_item{
  background-color: #ddd;
  width: 275px;
  height: 180px;
}

.main_center .left .subAd .ad_item img{
  width: 100%;
  height: 100%;
}


/***** 顶的问题分类 *******/
.main_center .left .ask_category{
  background-color: #fff;
  padding: 20px 0px 10px;
  display: flex;
  flex-direction: row;
}
.main_center .left .ask_category .warp{
  /*background-color: #eee;*/
  padding: 8px 10px;
  width: 100%;
  display: flex;
  flex-direction: row;
  font-size: 15px;
  border-radius: 2px;
}

.main_center .left .ask_category .warp span{
  padding: 0px 20px;
  font-weight: bold;
}

.main_center .left .ask_category .warp .selectd{
  color: #0089e4;
  /*border-bottom: 1px solid #0089e4;*/
}

.main_center .left .ask_tab{
  background-color: #fff;
  padding: 15px 30px;
  margin-bottom: -20px;
  border-bottom: 1px solid #ddd;
  display: flex;
  flex-direction: row;
}

.main_center .left .ask_tab span{
  padding: 3px 10px;
  border-radius: 2px;
  margin-right: 20px;
}

.main_center .left .ask_tab .selectd{
  background-color: #0089e4;
  color: #fff;
}

.main_center .right{
  min-width: 300px;
  width: 300px;
  /*background: #fff;*/
  /*height: 400px;*/
}

/***** 顶部的 nav_tab *****/
.nav_tab{
  height: 35px;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
}
.nav_tab i{
  font-style: normal;
  height: 35px;
  width: 98%;
  line-height: 35px;
  margin: 0 5px;
  /*border-bottom: 2px solid red;*/
}

.nav_tab .selectd{
  border-bottom: 2px solid red;
}



.ask_list{
  margin-top: 20px;
}

/******  右边的分类 *******/
.right .category{
  padding: 20px;
}

.right .category h3{
  padding: 10px 0px;
  text-align: left;
  font-size: 22px;
  color: #999;
  border-bottom: 3px solid #000;
}

.right .category h3 i{
  font-style: normal;
  font-size: 26px;
  font-weight: bold;
  color: #000;
}

.right .category .item{
  background-color: #f8f8f9;
  padding: 10px 15px;
}




</style>



