<template>
  <div class="baikeType">
    <h2>{{itemData.name}}</h2>
    <div>
      <span v-for="subItem in list" @click="itemAction(subItem)">{{subItem.name}}</span>
    </div>
  </div>
</template>

<script>
import {getStore,imgBaseUrl} from '../../../components/common/mUtils'
export default {
  data(){
    return{
      imgBaseUrl,
      data:[],
      list:[],
      isShowMreo:false,
      selfIndex:0,
    }
  },
  props:['itemData','index'],
  filters:{
    parseFloat(a){
      return parseFloat(a)
    },
    getNumber(idx){
      ++idx
      if (idx < 10) {
        idx = '0'+idx
      }
      return idx;
    }
  },
  computed:{
  },
  created(){
  },
  mounted(){
    this.getData()
  },
  components:{
  },
  methods: {
      setShowMreo(){
        this.isShowMreo = !this.isShowMreo
      },
      itemAction(subItem){
        subItem.titleName = this.itemData.name
        this.$emit('itemAction',subItem)
      },
      getData(){
        // Indicator.open();
        let getUrl = this.CFWConfig.APIHost + '/v3/ask/zi?id='+this.itemData.ask_type_id
        this.$http.get(getUrl).then(response => {
          let code = response.data.code
          if (code == 200) {
            this.list = response.data.result
          }
          // Indicator.close();
        }, response => {
          Toast('请求失败');
          console.log(response);
          // Indicator.close();
        });
    }
  },
  watch: {
  }

}

</script>

<style scoped>

.baikeType h2{
  width: 100%;
  font-size: 18px;
  font-weight: bold;
  text-align: left;
  margin: 10px 0px;
}
.baikeType div{
  display: flex;
  flex-direction: row;
  flex-wrap:wrap;

}

.baikeType div span{
  font-size: 14px;
  width: calc(99%/3);
  line-height: 30px;
  text-align: left;
}

.baikeType div span:hover{
  /*border-bottom: 1px solid #eee;*/
  color: #000;
  font-weight: bold;
}

</style>



